<template>
  <v-dialog
    :model-value="show"
    @update:model-value="updateShow"
    max-width="500px"
  >
    <v-card>
      <v-card-title class="text-h5">
        <span>{{
          item?.isNew ? "Add New User" : `${item?.user} Details`
        }}</span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-row justify="center">
            <v-col cols="12">
              <v-text-field
                v-if="item"
                v-model="localUsername"
                label="Username"
                variant="outlined"
                class="mb-3"
              ></v-text-field>
              <v-text-field
                v-if="item"
                v-model.number="localDkp"
                label="DKP"
                type="number"
                variant="outlined"
                class="mb-3"
              ></v-text-field>
              <v-text-field
                v-if="item"
                v-model="localClass"
                label="Class"
                variant="outlined"
                class="mb-3"
              ></v-text-field>
              <v-text-field
                v-if="item"
                v-model="localLevel"
                label="Level"
                variant="outlined"
                class="mb-3"
              ></v-text-field>
              <div class="text-subtitle-2 mb-2">
                Total Event Attendance: {{ attendanceCount }}
              </div>
            </v-col>
            <v-col cols="12" class="attendance-list pa-4">
              <v-list-subheader>Attendance by Event Type:</v-list-subheader>
              <v-table dense>
                <template #header>
                  <tr>
                    <th>Event Type</th>
                    <th>Attendance Count</th>
                  </tr>
                </template>
                <template #default>
                  <tr v-for="(count, type) in attendanceByType" :key="type">
                    <td>{{ type }}</td>
                    <td>{{ count }}</td>
                  </tr>
                </template>
              </v-table>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" variant="text" @click="updateShow(false)">
          Cancel
        </v-btn>
        <v-btn color="primary" variant="text" @click="saveChanges">
          Save
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, watch, computed, defineProps, defineEmits } from "vue";
import { ref as dbRef, set, get } from "firebase/database";
import { inject } from "vue";

const props = defineProps({
  modelValue: {
    type: Boolean,
    required: true,
  },
  item: {
    type: Object,
    default: null,
  },
});

const emit = defineEmits(["update:modelValue", "saved"]);

// Computed property for v-model handling
const show = computed({
  get: () => props.modelValue,
  set: (value) => emit("update:modelValue", value),
});

const updateShow = (value) => {
  emit("update:modelValue", value);
};

// Get database instance at setup time
const db = inject("db");
const localDkp = ref(0);
const localUsername = ref("");
const localClass = ref("");
const localLevel = ref("");
const attendanceCount = ref(0);
const attendanceByType = ref({});
const eventTypesToDisplay = [
  "Field Raids",
  "Epics",
  "Clan Hall/Fort",
  "Call To Arms",
  "Siege",
];

const fetchAttendanceCount = async () => {
  if (!props.item || !db) return;

  try {
    const eventsRef = dbRef(db, "events/");
    const snapshot = await get(eventsRef);

    if (snapshot.exists()) {
      const events = snapshot.val();

      let total = 0;
      // Initialize byType with all event types set to 0
      const byType = {};
      eventTypesToDisplay.forEach((type) => {
        byType[type] = 0;
      });

      // Count events where this user was present, grouped by title
      Object.values(events).forEach((event) => {
        console.log(
          "Event:",
          event.messageTitle,
          "UsersAttended:",
          event.usersAttended
        );
        if (event.usersAttended) {
          const title = event.messageTitle || "Untitled Event";
          console.log(
            "Event title:",
            title,
            "Is in display list:",
            eventTypesToDisplay.includes(title)
          );

          // Only count events that are in our eventTypesToDisplay list
          if (!eventTypesToDisplay.includes(title)) {
            return;
          }

          // Check if any user in usersAttended matches our user's ID or username
          const hasAttended = Object.values(event.usersAttended).some(
            (user) => {
              console.log("Checking user:", user, "against item:", props.item);
              return (
                user.userId === props.item.id ||
                user.userId === props.item.user ||
                user.username === props.item.user ||
                user.username === props.item.id
              );
            }
          );

          if (hasAttended) {
            byType[title] = (byType[title] || 0) + 1;
            total++;
            console.log(
              "User attended event:",
              title,
              "New count:",
              byType[title]
            );
          }
        }
      });

      console.log("Final attendance by type:", byType);
      console.log("Total attendance:", total);
      attendanceCount.value = total;
      attendanceByType.value = byType;
    }
  } catch (error) {
    console.error("Error fetching attendance:", error);
  }
};

// Watch for item changes to update local values
watch(
  () => props.item,
  async (newItem) => {
    if (newItem) {
      localDkp.value = newItem.dkp || 0;
      localUsername.value = newItem.user || "";
      localClass.value = newItem.class || "";
      localLevel.value = newItem.level || "";
      await fetchAttendanceCount();
    }
  },
  { immediate: true }
);

const saveChanges = async () => {
  if (!props.item || !db || !localUsername.value) return;

  try {
    // For new users, use the username as the ID; for existing users, use the existing ID
    const userId = props.item.isNew ? localUsername.value : props.item.id;
    const userRef = dbRef(db, `dkp/${userId}`);

    await set(userRef, {
      user: localUsername.value,
      dkp: Number(localDkp.value),
      class: localClass.value || "",
      level: localLevel.value || "",
    });

    emit("update:modelValue", false);
    emit("saved");
  } catch (error) {
    console.error("Error saving user:", error);
  }
};
</script>

<style scoped>
.v-card {
  background-color: #0a1025 !important;
  border: #1f9ee5 2px ridge;
  box-shadow: #1f9ee5 0px 0px 30px 2px;
  color: #eaf1ff;
}

.v-card-title {
  color: #eaf1ff !important;
  border-bottom: 1px solid #1f9ee5;
}

.attendance-list {
  border-radius: 4px;
  background-color: rgba(31, 158, 229, 0.05);
  border: #1f9ee5 1px solid;
  max-height: 200px;
  overflow-y: auto;
}

.v-text-field {
  color: #eaf1ff !important;
}

.v-text-field:deep(input) {
  color: #eaf1ff !important;
}

.v-text-field:deep(label) {
  color: #1f9ee5 !important;
}

.v-text-field:deep(.v-field__outline) {
  color: #1f9ee5 !important;
}

.v-table {
  background-color: transparent !important;
  color: #eaf1ff !important;
}

.v-table:deep(th) {
  background-color: rgba(31, 158, 229, 0.1) !important;
  color: #eaf1ff !important;
  border-bottom: 1px solid #1f9ee5 !important;
}

.v-table:deep(td) {
  color: #eaf1ff !important;
  border-bottom: 1px solid rgba(31, 158, 229, 0.3) !important;
}

.v-btn {
  color: #1f9ee5 !important;
}

.v-list-subheader {
  color: #1f9ee5 !important;
  font-weight: bold;
}

.text-subtitle-2 {
  color: #eaf1ff !important;
}
</style>
