<template>
  <v-container fluid class="pa-0">
    <v-row>
      <v-col cols="12">
        <div
          class="d-flex justify-space-between align-center py-2 header-container"
        >
          <h2 class="text-h4 font-weight-bold page-title">Party Members</h2>
          <div class="d-flex align-center gap-4 button-group">
            <v-btn
              @click="addParty"
              variant="tonal"
              color="#1f9ee5"
              prepend-icon="mdi-plus"
            >
              Add Party
            </v-btn>
            <v-btn
              @click="saveParties"
              variant="tonal"
              color="success"
              prepend-icon="mdi-content-save"
              :loading="saving"
              class="mx-4"
            >
              Save Parties
            </v-btn>
          </div>
        </div>
        <hr />
      </v-col>
    </v-row>

    <v-row>
      <!-- Unassigned Users Pool -->
      <v-col cols="12" md="12" lg="3">
        <v-card class="party-card" id="user-pool-card">
          <v-card-title class="text-center">Available Users</v-card-title>
          <v-card-text>
            <draggable
              v-model="unassignedUsers"
              group="users"
              item-key="username"
              class="user-pool"
            >
              <template #item="{ element }">
                <v-card class="user-card mb-2">
                  <v-card-text class="d-flex align-center">
                    <div class="d-flex flex-column">
                      <div class="d-flex align-center">
                        <v-icon color="#1f9ee5" class="me-2"
                          >mdi-account</v-icon
                        >
                        {{ element.username }}
                      </div>
                      <div
                        class="user-details"
                        v-if="element.class || element.level"
                      >
                        <span v-if="element.class">{{ element.class }}</span>
                        <span v-if="element.class && element.level">  </span>
                        <span v-if="element.level"
                          >Level {{ element.level }}</span
                        >
                      </div>
                    </div>
                  </v-card-text>
                </v-card>
              </template>
            </draggable>
          </v-card-text>
        </v-card>
      </v-col>

      <!-- Party Groups -->
      <v-col cols="12" md="12" lg="9">
        <v-row style="width: 100%">
          <v-col
            v-for="(party, index) in parties"
            :key="index"
            cols="12"
            sm="6"
            lg="4"
          >
            <v-card class="party-card">
              <v-card-title class="text-center party-title-container">
                <div class="d-flex align-center justify-space-between w-100">
                  <v-text-field
                    v-model="party.name"
                    variant="plain"
                    class="party-name-field"
                    hide-details
                    density="compact"
                    @click.stop
                  />
                  <div class="d-flex align-center">
                    <span class="party-count me-2"
                      >({{ party.members.length }})</span
                    >
                    <v-btn
                      icon
                      size="small"
                      variant="text"
                      color="error"
                      @click="deleteParty(index)"
                    >
                      <v-icon size="medium">mdi-close</v-icon>
                    </v-btn>
                  </div>
                </div>
              </v-card-title>
              <v-card-text>
                <draggable
                  v-model="party.members"
                  group="users"
                  item-key="username"
                  class="party-drop-zone"
                >
                  <template #item="{ element }">
                    <v-card class="user-card mb-1">
                      <v-card-text class="d-flex align-center">
                        <div class="d-flex flex-column">
                          <div class="d-flex align-center">
                            <v-icon color="#1f9ee5" class="me-2"
                              >mdi-account</v-icon
                            >
                            {{ element.username }}
                          </div>
                          <div
                            class="user-details"
                            v-if="element.class || element.level"
                          >
                            <span v-if="element.class">{{
                              element.class
                            }}</span>
                            <span v-if="element.class && element.level">
                              
                            </span>
                            <span v-if="element.level"
                              >Level {{ element.level }}</span
                            >
                          </div>
                        </div>
                      </v-card-text>
                    </v-card>
                  </template>
                </draggable>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>
<script setup>
import { ref, defineProps, watch, inject, onMounted } from "vue";
import {
  ref as dbRef,
  set,
  get,
  query,
  orderByKey,
  limitToLast,
} from "firebase/database";
import draggable from "vuedraggable";

const props = defineProps({
  users: {
    type: Array,
    default: () => [],
  },
});

// Get database instance
const db = inject("db");
const saving = ref(false);

const unassignedUsers = ref([]);
const parties = ref([
  { id: 1, name: "Party 1", members: [] },
  { id: 2, name: "Party 2", members: [] },
  { id: 3, name: "Party 3", members: [] },
  { id: 4, name: "Party 4", members: [] },
]);

// Load latest party configuration from database
const loadLatestPartyConfiguration = async (allUsers) => {
  if (!db) {
    // If no database, just put everyone in unassigned
    unassignedUsers.value = allUsers;
    return;
  }

  try {
    const partiesRef = dbRef(db, "parties");
    const latestQuery = query(partiesRef, orderByKey(), limitToLast(1));
    const snapshot = await get(latestQuery);

    if (snapshot.exists()) {
      const data = snapshot.val();
      const latestConfig = Object.values(data)[0]; // Get the most recent entry

      console.log("Loading saved party configuration:", latestConfig);

      // Create a map of saved users for quick lookup
      const savedUserMap = new Map();

      // Add unassigned users to map
      latestConfig.unassignedUsers?.forEach((user) => {
        savedUserMap.set(user.username, { ...user, assignment: "unassigned" });
      });

      // Add party members to map
      latestConfig.parties?.forEach((party) => {
        party.members?.forEach((user) => {
          savedUserMap.set(user.username, { ...user, assignment: party.id });
        });
      });

      // Update parties structure to match saved configuration
      if (latestConfig.parties && latestConfig.parties.length > 0) {
        parties.value = latestConfig.parties.map((party) => ({
          id: party.id,
          name: party.name || `Party ${party.id}`,
          members: [],
        }));
      }

      // Distribute users based on saved configuration
      const newUnassigned = [];
      const partyMembers = {};

      // Initialize party members arrays
      parties.value.forEach((party) => {
        partyMembers[party.id] = [];
      });

      allUsers.forEach((user) => {
        const savedUser = savedUserMap.get(user.username);
        if (savedUser && savedUser.assignment !== "unassigned") {
          // User was in a party
          if (partyMembers[savedUser.assignment]) {
            partyMembers[savedUser.assignment].push(user);
          } else {
            // Party doesn't exist anymore, put in unassigned
            newUnassigned.push(user);
          }
        } else {
          // User was unassigned or not found in saved config
          newUnassigned.push(user);
        }
      });

      // Apply the distribution
      unassignedUsers.value = newUnassigned;
      parties.value.forEach((party) => {
        party.members = partyMembers[party.id] || [];
      });

      console.log("Party configuration loaded successfully");
    } else {
      console.log("No saved party configuration found, using default");
      unassignedUsers.value = allUsers;
    }
  } catch (error) {
    console.error("Error loading party configuration:", error);
    // Fallback to putting everyone in unassigned
    unassignedUsers.value = allUsers;
  }
};

// Convert users prop to the format needed for drag and drop
const populateUsers = async () => {
  const allUsers = props.users
    .map((user) => ({
      username: user.user,
      class: user.class || "",
      level: user.level || "",
    }))
    .sort((a, b) => a.username.localeCompare(b.username));

  // Try to load saved party configuration
  await loadLatestPartyConfiguration(allUsers);
};

// Watch for changes in users prop
watch(
  () => props.users,
  async () => {
    await populateUsers();
  },
  { immediate: true }
);

// Ensure we load configuration when component mounts
onMounted(async () => {
  if (props.users.length > 0) {
    await populateUsers();
  }
});

// Function to add a new party group
const addParty = () => {
  const newId = Math.max(...parties.value.map((p) => p.id)) + 1;
  parties.value.push({
    id: newId,
    name: `Party ${newId}`,
    members: [],
  });
};

// Function to delete a party and move its members to unassigned (local only)
const deleteParty = (partyIndex) => {
  const partyToDelete = parties.value[partyIndex];

  // Move all party members to unassigned users
  unassignedUsers.value.push(...partyToDelete.members);

  // Remove the party from the array
  parties.value.splice(partyIndex, 1);

  console.log(
    `Party "${partyToDelete.name}" deleted locally. Click Save to persist changes.`
  );
};

// Function to save party configuration to database
const saveParties = async () => {
  if (!db) {
    console.error("Database not available");
    return;
  }

  saving.value = true;
  try {
    const now = new Date();
    const timestamp = now.toISOString();
    // Create Firebase-safe key by replacing invalid characters
    const firebaseKey = timestamp.replace(/[.#$[\]]/g, "_");

    const partyData = {
      timestamp,
      unassignedUsers: unassignedUsers.value,
      parties: parties.value.map((party) => ({
        id: party.id,
        name: party.name,
        members: party.members,
        memberCount: party.members.length,
      })),
    };

    const partiesRef = dbRef(db, `parties/${firebaseKey}`);
    await set(partiesRef, partyData);

    console.log("Parties saved successfully!", partyData);
    // You could add a success notification here
  } catch (error) {
    console.error("Error saving parties:", error);
    // You could add an error notification here
  } finally {
    saving.value = false;
  }
};
</script>

