<template>
  <v-app>
    <v-app-bar class="px-0">
      <v-container id="app-bar" fluid>
        <div class="d-flex justify-space-between align-center my-3">
          <div class="d-flex align-center">
            <span v-if="currentUser">Welcome, {{ currentUser.email }}</span>
            <span v-else>Please sign in</span>
          </div>
          <v-btn
            v-if="currentUser"
            @click="signOutUser"
            variant="tonal"
            class="sign-out-btn"
          >
            Sign Out
          </v-btn>
        </div>
        <v-tabs
          v-if="currentUser"
          v-model="view"
          color="#1f9ee5"
          align-tabs="left"
          class="mt-8"
        >
          <v-tab value="dkp-table" class="custom-tab">Guild Members</v-tab>
          <v-tab value="parties" class="custom-tab">Party Manager</v-tab>
        </v-tabs>
      </v-container>
    </v-app-bar>

    <v-main class="mt-12">
      <v-container id="main-container">
        <LogIn v-if="!currentUser" />
        <div v-else-if="loading" class="text-center pa-4">
          <v-progress-circular
            indeterminate
            color="#1f9ee5"
          ></v-progress-circular>
          <p class="mt-2" style="color: #eaf1ff">Loading data...</p>
        </div>
        <DkpTable
          v-else-if="view === 'dkp-table'"
          :users="users"
          :events="events"
          :event-types="eventTypes"
          :attendance-data="attendanceData"
          @user-updated="handleUserUpdate"
        />
        <PartyManager
          v-else-if="view === 'parties'"
          :users="users"
          @user-updated="handleUserUpdate"
        />
      </v-container>
    </v-main>
  </v-app>
</template>

<script setup>
import { useAuth } from "@/composables/useAuth";
import { ref, inject, onMounted, watch } from "vue";
import { signOut } from "firebase/auth";
import { ref as dbRef, onValue, get } from "firebase/database";
import LogIn from "./components/LogIn.vue";
import DkpTable from "./components/DkpTable.vue";
import PartyManager from "./components/PartyManager.vue";

const { auth, currentUser } = useAuth();
const view = ref("dkp-table");

// Centralized data state
const users = ref([]);
const events = ref([]);
const eventTypes = ref([]);
const attendanceData = ref({});
const loading = ref(true);

// Get database instance
const db = inject("db");

// Centralized data fetching
const fetchEvents = async () => {
  if (!db) return;

  try {
    const eventsRef = dbRef(db, "events/");
    const snapshot = await get(eventsRef);

    if (snapshot.exists()) {
      const eventsData = snapshot.val();
      events.value = eventsData;

      const types = new Set();
      const attendance = {};

      // First pass: collect all event types
      Object.values(eventsData).forEach((event) => {
        const title = event.messageTitle || "Untitled Event";
        types.add(title);
      });

      // Second pass: count attendance for each user and event type
      Object.values(eventsData).forEach((event) => {
        const title = event.messageTitle || "Untitled Event";
        if (event.usersAttended) {
          Object.values(event.usersAttended).forEach((user) => {
            if (!attendance[user.userId]) {
              attendance[user.userId] = {};
            }
            attendance[user.userId][title] =
              (attendance[user.userId][title] || 0) + 1;
          });
        }
      });

      eventTypes.value = Array.from(types);
      attendanceData.value = attendance;
    }
  } catch (error) {
    console.error("Error fetching events:", error);
  }
};

const fetchUsers = async () => {
  if (!db) return;

  try {
    const userRef = dbRef(db, "dkp/");
    return onValue(
      userRef,
      (snapshot) => {
        const data = snapshot.val();
        if (data) {
          const processedData = Object.entries(data).map(([key, value]) => {
            const baseData = {
              id: key,
              ...value,
            };

            // Add attendance data for each event type
            eventTypes.value.forEach((type) => {
              baseData[`event_${type}`] =
                attendanceData.value[key]?.[type] || 0;
            });

            return baseData;
          });

          users.value = processedData.sort((a, b) =>
            a.user.localeCompare(b.user)
          );
        } else {
          users.value = [];
        }
        loading.value = false;
      },
      (error) => {
        console.error("Database read error:", error);
        loading.value = false;
      }
    );
  } catch (error) {
    console.error("Error setting up database listener:", error);
    loading.value = false;
  }
};

// Initialize data when user is authenticated
watch(currentUser, async (user) => {
  if (user) {
    loading.value = true;
    await fetchEvents();
    await fetchUsers();
  }
});

onMounted(async () => {
  if (currentUser.value) {
    loading.value = true;
    await fetchEvents();
    await fetchUsers();
  }
});

const signOutUser = async () => {
  try {
    await signOut(auth);
    console.log("User signed out successfully.");
  } catch (error) {
    console.error("Error signing out:", error.message);
  }
};

// Event handlers for child components
const handleUserUpdate = () => {
  // Refresh data when a user is updated
  fetchUsers();
};
</script>

