<template>
  <v-container class="login-container d-flex align-center justify-center">
    <v-card class="login-card pa-8" max-width="400" width="100%">
      <v-card-title class="text-center mb-6">
        <h2 class="login-title">The White Order</h2>
        <div class="login-subtitle">DKP Tracker Authentication</div>
      </v-card-title>

      <v-card-text>
        <v-form @submit.prevent>
          <v-text-field
            v-model="email"
            label="Email"
            type="email"
            variant="outlined"
            class="mb-4 login-field"
            hide-details
            prepend-inner-icon="mdi-email"
          />

          <v-text-field
            v-model="password"
            label="Password"
            type="password"
            variant="outlined"
            class="mb-6 login-field"
            hide-details
            prepend-inner-icon="mdi-lock"
          />

          <div class="d-flex flex-column gap-3">
            <v-btn
              @click="signIn"
              color="#1f9ee5"
              variant="elevated"
              size="large"
              block
              class="login-btn"
              prepend-icon="mdi-login"
            >
              Sign In
            </v-btn>

            <v-btn
              @click="signUp"
              color="#1f9ee5"
              variant="outlined"
              size="large"
              block
              class="signup-btn"
              prepend-icon="mdi-account-plus"
            >
              Sign Up
            </v-btn>
          </div>

          <v-alert
            v-if="errorMsg"
            type="error"
            variant="tonal"
            class="mt-4"
            :text="errorMsg"
          />
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import { defineComponent, ref, inject } from "vue";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
} from "firebase/auth";
import { ref as dbRef, set } from "firebase/database"; // Import for DB access

export default defineComponent({
  name: "LogIn",
  setup() {
    const email = ref("");
    const password = ref("");
    const errorMsg = ref(null);

    // Inject the Firebase Auth and DB instances
    const auth = inject("auth");
    const db = inject("db");

    if (!auth || !db) {
      console.error("Firebase Auth or Database not provided!");
      errorMsg.value = "Authentication services not available.";
      return {}; // Return empty if services aren't available
    }

    const signUp = async () => {
      errorMsg.value = null; // Clear previous errors
      try {
        const userCredential = await createUserWithEmailAndPassword(
          auth,
          email.value,
          password.value
        );
        const user = userCredential.user;
        const currentUid = user.uid;
        console.log("User signed up! UID:", currentUid);

        // Store initial user data in Realtime Database
        const userProfileRef = dbRef(db, `users/${currentUid}/profile`);
        await set(userProfileRef, {
          email: user.email,
          createdAt: new Date().toISOString(),
        });
        console.log("User profile created in RTDB.");
      } catch (error) {
        console.error("Error signing up:", error.message);
        errorMsg.value = error.message;
      }
    };

    const signIn = async () => {
      errorMsg.value = null; // Clear previous errors
      try {
        await signInWithEmailAndPassword(auth, email.value, password.value);
        console.log("User signed in!");
      } catch (error) {
        console.error("Error signing in:", error.message);
        errorMsg.value = error.message;
      }
    };

    return { email, password, signUp, signIn, errorMsg };
  },
});
</script>

<style scoped>
.login-container {
  background-color: transparent;
}

.login-card {
  background-color: #0a1025 !important;
  border: #1f9ee5 2px solid;
  box-shadow: #1f9ee5 0px 0px 15px 3px;
  border-radius: 15px;
}

.login-title {
  color: #eaf1ff !important;
  font-weight: bold;
  font-size: 1.8rem;
  margin-bottom: 8px;
}

.login-subtitle {
  color: #1f9ee5 !important;
  font-size: 0.9rem;
  font-weight: 500;
}

.login-field :deep(.v-field) {
  background-color: rgba(31, 158, 229, 0.05) !important;
  border: #1f9ee5 1px solid;
  border-radius: 8px;
}

.login-field :deep(.v-field--focused) {
  border: #1f9ee5 2px solid;
  box-shadow: #1f9ee5 0px 0px 8px 1px;
}

.login-field :deep(.v-field__input) {
  color: #eaf1ff !important;
}

.login-field :deep(.v-label) {
  color: #1f9ee5 !important;
}

.login-field :deep(.v-icon) {
  color: #1f9ee5 !important;
}

.login-btn {
  background-color: #1f9ee5 !important;
  color: #eaf1ff !important;
  font-weight: bold;
  box-shadow: #1f9ee5 0px 0px 10px 2px;
}

.login-btn:hover {
  background-color: #0d7cc7 !important;
  transform: translateY(-2px);
  box-shadow: #1f9ee5 0px 4px 15px 3px;
}

.signup-btn {
  border: #1f9ee5 2px solid !important;
  color: #1f9ee5 !important;
  font-weight: bold;
}

.signup-btn:hover {
  background-color: rgba(31, 158, 229, 0.1) !important;
  transform: translateY(-2px);
  box-shadow: #1f9ee5 0px 0px 10px 2px;
}

:deep(.v-alert) {
  background-color: rgba(244, 67, 54, 0.1) !important;
  border: #f44336 1px solid;
  color: #ff6b6b !important;
}

:deep(.v-btn .v-icon) {
  color: inherit !important;
}
</style>
