<template>
  <v-card class="pa-4" id="card-container">
    <template #title>
      <div class="d-flex py-2">
        <h2 class="text-h4 font-weight-bold page-title">
          The White Order Lineage 2 Users
        </h2>
      </div>
      <hr />
    </template>
    <template v-slot:text>
      <div class="search-container">
        <v-text-field
          v-model="search"
          label="Search"
          variant="underlined"
          hide-details
          single-line
          class="search-field"
        >
          <template #prepend-inner>
            <v-icon color="#1f9ee5">mdi-magnify</v-icon>
          </template>
        </v-text-field>
      </div>
    </template>

    <v-data-table
      hover
      :headers="headers"
      :items="users"
      :search="search"
      fixed-header
      class="table-container"
      density="comfortable"
      :show-expand="isMobile"
      v-model:expanded="expanded"
      items-per-page="1000"
      hide-default-footer=""
    >
      <!-- Using CSS-based grouping instead of template override to preserve sorting -->
      <template #[`item.user`]="{ item }">
        <div class="d-flex align-center justify-center">
          <v-icon color="#1f9ee5" class="me-2">mdi-account</v-icon>
          {{ item.user }}
        </div>
      </template>
      <template #[`item.class`]="{ item }">
        <div class="text-center">
          {{ item.class || "-" }}
        </div>
      </template>
      <template #[`item.level`]="{ item }">
        <div class="text-center">
          {{ item.level || "-" }}
        </div>
      </template>
      <template #[`item.actions`]="{ item }" v-if="!isMobile">
        <v-btn
          size="small"
          icon
          variant="text"
          color="#1f9ee5"
          @click="openModal(item)"
        >
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
      </template>

      <!-- Mobile expanded row content -->
      <template #expanded-row="{ columns, item }">
        <tr>
          <td :colspan="columns.length" class="pa-4">
            <v-card class="mobile-expanded-card" flat>
              <v-card-text class="pa-3">
                <div class="d-flex justify-space-between align-center mb-3">
                  <h4 class="mobile-expanded-title">{{ item.user }} Details</h4>
                  <v-btn
                    size="small"
                    icon
                    variant="text"
                    color="#1f9ee5"
                    @click="openModal(item)"
                  >
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                </div>

                <div class="mb-3">
                  <div class="d-flex align-center mb-2">
                    <v-icon color="#1f9ee5" class="me-2" size="small"
                      >mdi-star</v-icon
                    >
                    <span class="mobile-label">DKP:</span>
                    <span class="mobile-value">{{ item.dkp || 0 }}</span>
                  </div>
                </div>

                <div class="mb-3">
                  <div class="d-flex align-center mb-2">
                    <v-icon color="#1f9ee5" class="me-2" size="small"
                      >mdi-calendar</v-icon
                    >
                    <span class="mobile-label">Event Attendance:</span>
                  </div>
                  <div class="pl-6">
                    <div
                      v-for="eventType in eventTypesToDisplay"
                      :key="eventType"
                      class="d-flex justify-space-between mb-1"
                    >
                      <span class="mobile-event-name">{{ eventType }}:</span>
                      <span class="mobile-event-count">{{
                        item[`event_${eventType}`] || 0
                      }}</span>
                    </div>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </td>
        </tr>
      </template>
    </v-data-table>

    <DkpModal v-model="dialog" :item="selectedItem" @saved="handleUserSaved" />
  </v-card>
</template>
<script setup>
import {
  ref,
  computed,
  defineProps,
  defineEmits,
  onMounted,
  onUnmounted,
} from "vue";
import DkpModal from "./DkpModal.vue";
//eslint-disable-next-line no-unused-vars
const props = defineProps({
  users: {
    type: Array,
    default: () => [],
  },
  events: {
    type: Object,
    default: () => ({}),
  },
  eventTypes: {
    type: Array,
    default: () => [],
  },
  attendanceData: {
    type: Object,
    default: () => ({}),
  },
});

const emit = defineEmits(["user-updated"]);

const search = ref("");
const dialog = ref(false);
const selectedItem = ref(null);
const isMobile = ref(false);
const expanded = ref([]);

// Mobile detection
const checkMobile = () => {
  isMobile.value = window.innerWidth < 768;
};

onMounted(() => {
  checkMobile();
  window.addEventListener("resize", checkMobile);
});

onUnmounted(() => {
  window.removeEventListener("resize", checkMobile);
});

const openModal = (item) => {
  selectedItem.value = item;
  dialog.value = true;
};

const eventTypesToDisplay = [
  "Field Raids",
  "Epics",
  "Clan Hall/Fort",
  "Call To Arms",
  "Siege",
];

const headers = computed(() => {
  const baseHeaders = [
    {
      align: "center",
      key: "user",
      sortable: true,
      title: "User Name",
      width: isMobile.value ? "40%" : "150px",
    },
    {
      key: "class",
      align: "center",
      title: "Class",
      sortable: true,
      width: isMobile.value ? "30%" : "120px",
    },
    {
      key: "level",
      align: "center",
      title: "Level",
      sortable: true,
      width: isMobile.value ? "30%" : "80px",
    },
  ];

  // On mobile, only show basic info + expand column
  if (isMobile.value) {
    return [
      ...baseHeaders,
      {
        key: "data-table-expand",
        align: "center",
        title: "",
        width: "48px",
        sortable: false,
      },
    ];
  }

  // On desktop, show all columns
  baseHeaders.push({
    key: "dkp",
    align: "center",
    title: "DKP",
    width: "100px",
    sortable: true,
  });

  // Add headers for each event type
  const eventHeaders = eventTypesToDisplay.map((type) => ({
    key: `event_${type}`,
    align: "center",
    title: type,
    sortable: true,
    width: "100px",
  }));

  return [
    ...baseHeaders,
    ...eventHeaders,
    {
      key: "actions",
      align: "center",
      title: "Actions",
      width: "80px",
    },
  ];
});

const handleUserSaved = () => {
  emit("user-updated");
};
</script>

